import './PDFViewer.css';

import React from 'react';
import { useState, useEffect } from 'react';
import { Document, Page } from 'react-pdf';

import { pdfjs } from 'react-pdf';

pdfjs.GlobalWorkerOptions.workerSrc =
`//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

const corsProxyUrl = 'https://cors-anywhere.herokuapp.com/';

// pdfjs.GlobalWorkerOptions.workerSrc = new URL(
//   'pdfjs-dist/build/pdf.worker.min.js',
//   import.meta.url,
// ).toString();
// const pdfjs = await import('pdfjs-dist/build/pdf');
// const pdfjsWorker = await import('pdfjs-dist/build/pdf.worker.entry');
//
// pdfjs.GlobalWorkerOptions.workerSrc = pdfjsWorker;
// <Document file={`${corsProxyUrl}${encodeURIComponent(pdfUrl)}`}>

function PDFViewer({ pdfUrl }) {
  const [pdfData, setPdfData] = useState(null);

  useEffect(() => {
    async function fetchPdfData() {
      try {
        const response = await fetch(`${corsProxyUrl}${encodeURIComponent('https://www.africau.edu/images/default/sample.pdf')}`);
        const pdfArrayBuffer = await response.arrayBuffer();
        setPdfData(pdfArrayBuffer);
      } catch (error) {
        console.error(error);
      }
    }

    fetchPdfData();
  }, []);

  if (!pdfData) {
    return <div>ORIT Loading PDF...</div>;
  }

  return (
    <div className="pdf-viewer">
      <Document file={pdfUrl}>
        <Page pageNumber={1} />
      </Document>
    </div>
  );
}

export default PDFViewer;
